<template>
  <div class="map-projection-container">
    <v-chart class="chart" :init-options="initEcharts" :option="option" />
  </div>
</template>

<script>
import FuJianJson from "@/static/ego_json_fu_jian.json";
export default {
  props: {
    // 图表数据
    dataSource: {
      type: [Array, Object],
      default: () => [
        // { name: "福州市", value: 0 },
        // { name: "厦门市", value: 100 },
        // { name: "莆田市", value: 200 },
        // { name: "三明市", value: 300 },
        // { name: "泉州市", value: 400 },
        // { name: "漳州市", value: 500 },
        // { name: "南平市", value: 600 },
        // { name: "龙岩市", value: 700 },
        // { name: "宁德市", value: 800 },
      ],
    },
  },
  watch: {
    dataSource() {
      this.getMaxData(this.dataSource);
    },
    maxData() {
      this.optionInit();
    },
  },
  data() {
    return {
      // 初始化对象
      initEcharts: {
        // devicePixelRatio: number, // 设备像素比，默认取浏览器的值window.devicePixelRatio。
        renderer: "canvas", // 渲染模式，支持'canvas'或者'svg'。
        useDirtyRect: true, // 是否开启脏矩形渲染，只有在 Canvas 渲染模式有效，默认为false。
        ssr: false, // 是否使用服务端渲染，只有在 SVG 渲染模式有效。开启后不再会每帧自动渲染，必须要调用 renderToSVGString 方法才能得到渲染后 SVG 字符串。
        width: "auto", // 可显式指定实例宽度，单位为像素。如果传入值为null/undefined/'auto'，则表示自动取 dom（实例容器）的宽度。
        height: "auto", // 可显式指定实例高度，单位为像素。如果传入值为null/undefined/'auto'，则表示自动取 dom（实例容器）的高度。
        locale: "ZH", // 使用的语言，内置 'ZH' 和 'EN' 两个语言，也可以使用 echarts.registerLocale 方法注册新的语言包。目前支持的语言见 src/i18n。
      },
      // echarts配置对象
      option: {},
      // 最大阈值
      maxData: 1000,
    };
  },
  created() {
    this.getMaxData(this.dataSource);
    this.$echarts.registerMap("FuJian", FuJianJson);
    this.optionInit();
  },
  methods: {
    /**
     * 初始化Echarts配置
     */
    optionInit() {
      setTimeout(() => {
        this.option = {
          tooltip: {
            trigger: "item",
            showDelay: 0,
            transitionDuration: 0.2,
          },
          visualMap: {
            left: "right",
            min: 0,
            max: this.maxData,
            inRange: {
              color: [
                "#313695",
                "#4575b4",
                "#74add1",
                "#abd9e9",
                "#e0f3f8",
                "#ffffbf",
                "#fee090",
                "#fdae61",
                "#f46d43",
                "#d73027",
                "#a50026",
              ],
            },
            text: ["High", "Low"],
            textStyle: {
              color: "#D6F7FE",
            },
            calculable: true,
          },
          series: [
            {
              name: "覆盖人次",
              type: "map",
              map: "FuJian",
              ego: {
                project: function (point) {
                  return projection(point);
                },
                unproject: function (point) {
                  return projection.invert(point);
                },
              },
              emphasis: {
                label: {
                  show: true,
                },
              },
              data: this.dataSource,
            },
          ],
        };
      }, 500);
    },

    getMaxData(arrParam) {
      const arr = [];

      for (const item of arrParam) {
        arr.push(item.value);
      }
      this.maxData = Math.max(...arr);
    },
  },
};
</script>

<style lang="scss" scoped>
.map-projection-container {
  .chart {
    width: 100%;
    height: px(568);
  }
}
</style>
